'use client'

import React, { useState, useEffect, useRef } from 'react'
import { Upload, Camera, Folder, FileText, Brain, Command, Lock } from 'lucide-react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Sidebar, SidebarContent, SidebarGroup, SidebarGroupContent, SidebarGroupLabel, SidebarMenu, SidebarMenuButton, SidebarMenuItem, SidebarProvider } from '@/components/ui/sidebar'

export default function Optidrive() {
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 })
  const containerRef = useRef(null)

  useEffect(() => {
    const handleMouseMove = (event) => {
      if (containerRef.current) {
        const rect = containerRef.current.getBoundingClientRect()
        setMousePosition({
          x: event.clientX - rect.left,
          y: event.clientY - rect.top,
        })
      }
    }

    window.addEventListener('mousemove', handleMouseMove)
    return () => {
      window.removeEventListener('mousemove', handleMouseMove)
    }
  }, [])

  return (
      <SidebarProvider>
        <div className="flex h-screen bg-blue-900 text-white" ref={containerRef}>
          <Sidebar className="w-64 bg-blue-800">
            <SidebarContent>
              <SidebarGroup>
                <SidebarGroupLabel>Tools</SidebarGroupLabel>
                <SidebarGroupContent>
                  <SidebarMenu>
                    <SidebarMenuItem>
                      <SidebarMenuButton>
                        <Brain className="mr-2 h-4 w-4" />
                        <span>Analyze with AI</span>
                      </SidebarMenuButton>
                    </SidebarMenuItem>
                    <SidebarMenuItem>
                      <SidebarMenuButton>
                        <Command className="mr-2 h-4 w-4" />
                        <span>Command Center</span>
                      </SidebarMenuButton>
                    </SidebarMenuItem>
                    <SidebarMenuItem>
                      <SidebarMenuButton>
                        <Lock className="mr-2 h-4 w-4" />
                        <span>Lock</span>
                      </SidebarMenuButton>
                    </SidebarMenuItem>
                  </SidebarMenu>
                </SidebarGroupContent>
              </SidebarGroup>
            </SidebarContent>
          </Sidebar>
          <main className="flex-1 p-8">
            <h1 className="text-4xl font-bold mb-8">Optidrive</h1>
            <div className="grid grid-cols-2 gap-4 mb-8">
              <Button className="h-16 text-lg">
                <Upload className="mr-2 h-6 w-6" />
                Upload
              </Button>
              <Button className="h-16 text-lg">
                <Camera className="mr-2 h-6 w-6" />
                Open Camera
              </Button>
              <Button className="h-16 text-lg">
                <Folder className="mr-2 h-6 w-6" />
                Create Folder
              </Button>
              <Button className="h-16 text-lg">
                <FileText className="mr-2 h-6 w-6" />
                File Explorer
              </Button>
            </div>
            <div className="border-2 border-blue-500 rounded-lg p-8 text-center">
              <p className="text-xl mb-4">Drag and drop files here</p>
              <Input type="file" className="hidden" id="file-upload" multiple />
              <label htmlFor="file-upload" className="cursor-pointer bg-blue-600 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded">
                Select Files
              </label>
            </div>
          </main>
          <div
              className="pointer-events-none absolute inset-0 z-30 transition-opacity duration-300"
              style={{
                background: `radial-gradient(circle 200px at ${mousePosition.x}px ${mousePosition.y}px, rgba(59, 130, 246, 0.15), transparent 80%)`,
              }}
          />
        </div>
      </SidebarProvider>
  )
}

